/* Hype Home Banner */
.HYPE_document {
  margin: -5rem auto;
  position: relative;
  width: 900px;
  height: 300px;
  overflow: hidden;
}

/* Sudo SVG Logo */

.sudo-logo {
  width: 10rem;
  height: 10rem;
  position: absolute;
  top: 2rem;
  right: 13rem;
}

/* Background Clouds Positioning */

.top-bar {
  max-width: var(--width);
  height: 13rem;
}

.nav-bar {
  margin: -11rem auto ;
  max-width: var(--width) ;
}

.banner-box {
  max-width: var(--width);
  height: 3.5rem;
  margin: 12rem auto;
}


/* Banner */

.gallery-banner-box {
  max-width: var(--width);
  margin: 2% auto;
  background: var(--color-four);
}

.home-spacer {
  height: 3em;
}

.banner-text h1 {
  color: var(--color-zero);
}

.banner-text h2 {
  color: var(--color-zero);
}

.banner-text p {
  color: var(--color-four);
}

/* Artist Pic */
.artist-pic {
  width: 900px;
  height: 300px;
  margin: auto;
}

/* Main Area */
.regular-content {
  max-width: var(--flex-width);
  margin: -8rem auto;
}

.regular-content ul {
  list-style-type: none;
}

/* Content size to hold Sticky Footer */
.content {
  margin-bottom: 10rem;
}

/* Footer */

.footer {
  height: 10rem;
}

.footer-box {
  padding: 1.5rem;
}

.footer p {
  color: var(--color-one);
}

.footer a {
  color: var(--color-five);
}

/* Extras */
.spacer::before, .spacer::after {
  content: '';

  height: 0.09em;
  min-width: var(--flex-width); }
.spacer::before {
  background: linear-gradient(to right, rgba(101, 78, 163, 0), var(--color-four));
  margin-right: 4vh; }
.spacer::after {
  background: linear-gradient(to left, rgba(101, 78, 163, 0), var(--color-four));
  margin-left: 4vh; }

/* Text */
h1 {
  font-family: rubik;
  color: var(--color-zero);
  text-align: center;
  font-weight: 800;
  font-size: 3em;
   }

h2 {
  font-family: rubik;
  color: var(--color-zero);
  text-align: center;
  font-weight: 400;
  font-size: 1em;
   }

h3 {
  font-family: rubik;
  color: var(--color-zero);
  text-align: center;
  font-weight: 400;
  font-size: 2em;
   }

h4 {
  font-family: rubik;
  color: var(--color-five);
  text-align: center;
  font-weight: 600;
  font-size: 1em;
   }

p {
  font-family: rubik;
  color: var(--color-five);
  text-align: center;
  font-weight: 400;
  font-size: 1em;
   }

a {
  font-family: rubik;
  color: var(--color-one);
  font-weight: 400;
  font-size: 1em; }

/* Label Colours */
#post-title {
  color: var(--color-one);
  margin: 5% auto; }

#creator-name {
  color: var(--color-one); }

/* Photo Galleries */

#gallery_name {
  text-decoration-color: var(--color-one);
}

.galleries {
  margin: -6rem auto;
}

.gallery {
  max-width: var(--flex-width);
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10rem;
}

.gallery ul {
  list-style-type: none;
  max-width: var(--flex-width);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-bottom: 10rem;
}

.gallery-blurb {
  max-width: var(--flex-width);
  margin: auto;
}

.gallery-blurb h3 {
  color: var(--color-one);
}

.gallery>a, .gallery::after {
  flex-basis: var(--w);
}

.gallery>a {
  margin: 0.25rem;
  flex-grow: calc(var(--w) / var(--h) * 100);
  width: calc(var(--w) * 1px);
}

.gallery::after {
  --w: 2;
  --h: 1;
  content: '';
  flex-grow: 1000000;
}

.gallery>a>img {
  display: block;
  width: 100%;
}

.gallery-section h2 {
  text-align: center;
  margin-top: 30px;
}

.gallery-section h2 a {
  visibility: hidden;
  color: #aaaaaa;
}

.gallery-section h2 a:hover {
  color: #473772;
}

.gallery-section:hover h2 a {
  visibility: visible;
}

.gallery-section h2 a svg {
  height: 1.2rem
}

.gallery-section p {
  width: 70%;
  margin: 0px auto 20px;
}

.caption-date {
  font-size: 15px;
  font-weight: normal;
  margin-top: 3px;
  margin-bottom: 0px;
}

.image-description {
  font-size: 15px;
  font-weight: normal;
  margin-top: 3px;
  margin-bottom: 0px;
}
