
:root {
  --color-zero: white;
  --color-one: #654ea3; /* Purple Color */
  --color-two: #eaafc8; /* Pink Color */
  --color-three: #473772; /* Darker Purple Color */
  --color-four: #2B2D2F; /* This is the background color */
  --color-five: #7a8085; /* Lighter than background color */
  --color-six: #DFAF30; /* Gold Color */
  --color-seven: #586777; /* Blue Color */
  --color-eight: #654ea3; /* SVG Main Color */
  --color-nine: #494c4f; /* SVG Secondary Color */

}
